.firstpage-elements-container {
  height: 100%;

  .form-container {
    width: 100%;

    max-width: 600px;
    // padding-top: 2.5em;
    .sign-up-form {
      height: 100%;

      .form-input-one {
        display: flex;
        flex-direction: column;
        padding-top: 2.125em;
      }
      .form-input-two {
        display: flex;
        flex-direction: column;
        margin-top: 1.437em;
      }
      .form-input-three {
        display: flex;
        flex-direction: column;
        margin-top: 1.437em;
      }
      .form-label {
        color: #232323;
        font-size: 1.375rem;
        font-weight: 700;
        line-height: 1.688em;

        padding-bottom: 0.5em;
      }
      .firstName-input,
      .lastName-input,
      .email-input {
        color: #232323;
        font-weight: 400;
        font-size: 1.125rem;
        height: 50px;
        padding: 0rem 1.25rem;
        // margin-bottom: 2em;
        line-height: 1.375em;
        letter-spacing: 0em;
        background-color: #c4c4c400;
        border: 0.8px solid #232323;
        max-width: 513px; //26.71%;
        width: 90vw;
       
        outline: none;
      }
      .error-message {
        padding-left: 10px;
        padding-top: 5px;
        // margin-bottom: 1.375em;
        height: 1.188em;
        font-size: 1rem;
        color: #f15524;
      }
      .error-message-hidden {
        padding-left: 10px;
        padding-top: 5px;
        // margin-bottom: 1.375em;
        height: 1.188em;
        visibility: none;
      }

      .note-text {
        max-width: 365px;
        color: #626262;
        margin-top: 4.188em;
        padding-top: 1.25em;
        border-top: 0.8px solid #232323;
        font-weight: 400;
      }
    }
  }
}

/* Change the white to any color */
input:-webkit-autofill,
input:-webkit-autofill:hover {
  transition: background-color 5000s;
  -webkit-box-shadow: 0 0 0 30px #eaeaea inset !important;
}
input:-webkit-autofill:focus,
input:-webkit-autofill::first-line {
  font-size: 1.125rem;
}
input:-webkit-autofill:active {
  transition: background-color 5000s;
  -webkit-box-shadow: 0 0 0 30px #eaeaea inset !important;
}
