.header-container {
  flex-shrink: 0;

  width: 100%;
  height: 13%;

  padding: 5em 12.5em 0em 12.5em;
  @media only screen and (max-width: 1224px) {
    flex-shrink: 0;

    width: 100%;
    height: 13%;
    padding: 5em 6em 0em 6em;
  }
  @media only screen and (max-width: 768px) {
    flex-shrink: 0;

    width: 100%;
    height: 13%;
    padding: 4em 2em 0em 2em;
  }
  @media only screen and (max-width: 400px) {
    flex-shrink: 0;

    width: 100%;
    height: 13%;
    max-height: 80px;;
    padding: 4em 2em 0em 2em;
  }
  .redberry-header {
    display: flex;
    justify-content: space-between;

    border-bottom: 1.8px solid #232323;
  }
  .page-identifier {
    color: #232323;
    font-size: 2.5rem;
    font-weight: 600;
  }
  .horizontal-line {
    height: 1.8px;
  }
  .horizontal-line hr {
  }
}
